<template>
    <div class="flex mt-[4.7rem] md:mt-0 overflow-hidden">
        <div class="content">
            <h2 class="intro-y text-lg font-medium mt-10">
                User Data List
            </h2>
            <div class="grid grid-cols-12 gap-6 mt-5">
                <div class="intro-y col-span-12 flex flex-wrap sm:flex-nowrap items-center mt-2">
                    <router-link to="/admin/user-add">
                        <button class="btn btn-primary shadow-md mr-2">Add User</button>
                    </router-link>
                    <div class="dropdown">
                        <button class="dropdown-toggle btn px-2 box" aria-expanded="false" data-tw-toggle="dropdown">
                            <span class="w-5 h-5 flex items-center justify-center"> <svg xmlns="http://www.w3.org/2000/svg"
                                    width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor"
                                    stroke-width="2" stroke-linecap="round" stroke-linejoin="round" icon-name="plus"
                                    class="lucide lucide-plus w-4 h-4" data-lucide="plus">
                                    <line x1="12" y1="5" x2="12" y2="19"></line>
                                    <line x1="5" y1="12" x2="19" y2="12"></line>
                                </svg> </span>
                        </button>
                        <div class="dropdown-menu w-40">
                            <ul class="dropdown-content">
                                <li>
                                    <a href="" class="dropdown-item"> <i data-lucide="printer" class="w-4 h-4 mr-2"></i>
                                        Print </a>
                                </li>
                                <li>
                                    <a href="" class="dropdown-item"> <i data-lucide="file-text" class="w-4 h-4 mr-2"></i>
                                        Export to Excel </a>
                                </li>
                                <li>
                                    <a href="" class="dropdown-item"> <i data-lucide="file-text" class="w-4 h-4 mr-2"></i>
                                        Export to PDF </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="hidden md:block mx-auto text-slate-500"></div>
                    <div class="w-full sm:w-auto mt-3 sm:mt-0 sm:ml-auto md:ml-0">
                        <div class="w-56 relative text-slate-500">
                            <input type="text" class="form-control w-56 box pr-10" placeholder="Search..." @input="search"
                                v-model="query
                                ">
                            <i class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0" data-lucide="search"></i>
                        </div>
                    </div>
                </div>
                <!-- BEGIN: Data List -->
                <div class="intro-y col-span-12 overflow-auto lg:overflow-visible">
                    <table class="table table-report -mt-2">
                        <thead>
                            <tr>
                                <th class="whitespace-nowrap">SNO</th>
                                <th class="whitespace-nowrap">FIRST NAME</th>
                                <th class="whitespace-nowrap">LAST NAME</th>
                                <th class="text-center whitespace-nowrap">EMAIL</th>
                                <th class="text-center whitespace-nowrap">MOBILE NUMBER</th>
                                <th class="text-center whitespace-nowrap">ACTIONS</th>
                            </tr>
                        </thead>
                        <tbody v-for="(data, index) in user" :key=index>
                            <tr class="intro-x">
                                <td class="w-40">
                                    <div class="flex">
                                        {{ (currentPage - 1) * pageSize + index + 1 }}
                                    </div>
                                </td>
                                <td class="w-40">
                                    <div class="flex">
                                        {{ data.firstName }}
                                    </div>
                                </td>
                                <td>
                                    <a href="" class="font-medium whitespace-nowrap">{{ data.lastName }}</a>
                                </td>
                                <td class="text-center">{{ data.email }}</td>
                                <td class="w-40">
                                    <div class="flex items-center justify-center text-danger"> {{ data.phoneNumber }}</div>
                                </td>
                                <td class="table-report__action w-56">
                                    <div class="flex justify-center items-center">
                                        <router-link :to="{ path: '/admin/edit/user/' + data._id }">
                                            <a class="flex items-center mr-3" href="javascript:;"> <i
                                                    data-lucide="check-square" class="w-4 h-4 mr-1"></i>
                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24"
                                                    viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                    stroke-linecap="round" stroke-linejoin="round" icon-name="check-square"
                                                    data-lucide="check-square"
                                                    class="lucide lucide-check-square w-4 h-4 mr-1">
                                                    <polyline points="9 11 12 14 22 4"></polyline>
                                                    <path d="M21 12v7a2 2 0 01-2 2H5a2 2 0 01-2-2V5a2 2 0 012-2h11"></path>
                                                </svg>
                                                Edit </a>
                                        </router-link>
                                        <a class="flex items-center text-danger" href="javascript:;" data-tw-toggle="modal"
                                            data-tw-target="#delete-confirmation-modal"> <svg
                                                xmlns="http://www.w3.org/2000/svg" width="24" height="24"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round" icon-name="trash-2"
                                                data-lucide="trash-2" class="lucide lucide-trash-2 w-4 h-4 mr-1">
                                                <polyline points="3 6 5 6 21 6"></polyline>
                                                <path
                                                    d="M19 6v14a2 2 0 01-2 2H7a2 2 0 01-2-2V6m3 0V4a2 2 0 012-2h4a2 2 0 012 2v2">
                                                </path>
                                                <line x1="10" y1="11" x2="10" y2="17"></line>
                                                <line x1="14" y1="11" x2="14" y2="17"></line>
                                            </svg> Delete </a>
                                    </div>
                                    <div id="delete-confirmation-modal" class="modal" tabindex="-1" aria-hidden="true">
                                        <div class="modal-dialog">
                                            <div class="modal-content">
                                                <div class="modal-body p-0">
                                                    <div class="p-5 text-center">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24"
                                                            viewBox="0 0 24 24" fill="none" stroke="currentColor"
                                                            stroke-width="2" stroke-linecap="round" stroke-linejoin="round"
                                                            icon-name="x-circle" data-lucide="x-circle"
                                                            class="lucide lucide-x-circle w-16 h-16 text-danger mx-auto mt-3">
                                                            <circle cx="12" cy="12" r="10"></circle>
                                                            <line x1="15" y1="9" x2="9" y2="15"></line>
                                                            <line x1="9" y1="9" x2="15" y2="15"></line>
                                                        </svg>
                                                        <div class="text-3xl mt-5">Are you sure?</div>
                                                        <div class="text-slate-500 mt-2">
                                                            Do you really want to delete these records?
                                                            <br>
                                                            This process cannot be undone.
                                                        </div>
                                                    </div>
                                                    <div class="px-5 pb-8 text-center">
                                                        <button type="button" data-tw-dismiss="modal"
                                                            class="btn btn-outline-secondary w-24 mr-1">Cancel</button>
                                                        <button type="button" class="btn btn-danger w-24"
                                                            @click="deleteItem(data._id)">Delete</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </td>
                            </tr>

                        </tbody>
                    </table>
                </div>
                <!-- END: Data List -->
                <!-- BEGIN: Pagination -->
                <div class="intro-y col-span-12 flex flex-wrap sm:flex-row sm:flex-nowrap items-center">
                    <nav class="w-full sm:w-auto sm:mr-auto">
                        <div class="clearfix">
                            <ul class="pagination">
                                <button v-if="currentPage > 1" @click="changePage(currentPage - 1)"
                                    class="btn btn-primary">Prev</button>
                                <button v-for="pageNumber in totalPages" :key="pageNumber" @click="changePage(pageNumber)"
                                    class="btn btn-primary">{{
                                        pageNumber }}</button>
                                <button v-if="currentPage < totalPages" @click="changePage(currentPage + 1)"
                                    class="btn btn-primary">Next</button>
                            </ul>
                        </div>
                    </nav>

                </div>
                <!-- END: Pagination -->
            </div>

        </div>
    </div>
</template>

<script>
import axios from 'axios';
import {
    deleteRequest
} from '../ApiFunctions';
export default {

    name: 'UserDataList',
    data() {
        return {

            user: [],
            query: '',
            currentPage: 1,
            pageSize: 5,
            totalPages: 0,
            totalRecords: 0,
            NotFound: ''
        }
    },
    mounted() {
        this.search();
    },
    methods: {
        async search() {
            const response = await axios.get('http://localhost:8000/api/get-user', {
                params: {
                    query: this.query,
                    pageNumber: this.currentPage,
                    pageSize: this.pageSize
                }
            });
            this.user = response.data.data;
            this.totalPages = response.data.totalPages;
            this.totalRecords = response.data.count;

        },
        async changePage(pageNumber) {
            this.currentPage = pageNumber;
            await this.search()
        },

        async deleteItem(id) {
            await deleteRequest(`/remove-user/${id}`)
            this.search()

        },
    }
}
</script>



